import { service } from './http';

const getDefaultSearch = () => service.get('/api/client/defaultSearch/') //
const getSearchHistory = () => service.get('/api/client/searchHistory/') //
const clearSearchHistory = () => service.delete('/api/client/searchHistory/clear/') //
const getHotKeyword = () => service.get('/api/client/hotKeyword/') //

const getCourseSearch = (data) => service.get('/api/client/courses/search/', data) //es
const getSearchLike = (data) => service.get('/api/client/searchLike/', data) //

export {
  getDefaultSearch,
  getSearchHistory,
  clearSearchHistory,
  getHotKeyword,
  getCourseSearch,
  getSearchLike
}
